.home {
    .title {
        font-size: 1rem;
        background-color: #38539a;
        color: #fff;
        height: 50px;

        /*div {
            background-color: white;
            font-weight: normal;
            font-style: italic;
        } */
    }

    @media (max-width: 768px) {
        .date-event {
            font-weight: bold;
            // display: grid;
            // grid-template-columns: 50% 50%;

            .event-card {
                color: #000;
                border-radius: 25px;
                background-color: white;
                margin: 16px;
                padding: 8px;


                .event-card-detail {
                    display: inline-block;
                    //float: left;
                    width: 60%;
                    //size: 0em;
                    padding: 16px;
                    position: relative;
                    ;

                    .event-title {
                        font-size: 1rem;
                        color: #000;
                        //margin: 16px;
                    }

                    .datetime {
                        font-size: .875rem;
                    }
                }

                .event-card-img {
                    //display: inline-block;
                    position: relative;
                    float: right;
                    overflow: hidden;
                    padding: 0;
                }
            }
        }
    }

    @media (min-width: 768px) {
    .date-event {
        font-weight: bold;
        display: grid;
        grid-template-columns: 50% 50%;

        .event-card {
            color: #000;
            border-radius: 25px;
            background-color: white;
            margin: 16px;
            padding: 8px;


            .event-card-detail {
                display: inline-block;
                //float: left;
                width: 60%;
                //size: 0em;
                padding: 16px;
                position: relative;
                ;

                .event-title {
                    font-size: 1rem;
                    color: #000;
                    //margin: 16px;
                }

                .datetime {
                    font-size: .875rem;
                }
            }

            .event-card-img {
                //display: inline-block;
                position: relative;
                float: right;
                overflow: hidden;
                padding: 0;
            }
        }
    }
}




}